.set-container {
  display: grid;
  grid-gap: 10px;
  grid-auto-flow: dense;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
}

.landing-page {
  min-height: calc(100vh - 52px);
}

.drawer > div {
  background: #ebebeb !important;
  border-radius: 14px 14px 0 0;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}

.drawer.collapsed > div {
  visibility: unset !important;
  -webkit-transform: translateY(736px) translateY(-536px) !important;
  transform: translateY(736px) translateY(-536px) !important;
}

.drawer .MuiBackdrop-root {
  background-color: rgba(0, 0, 0, 0.5) !important;
}

.container {
  max-width: 700px;
}

#drawer-title {
  width: 100%;
  text-align: center;
  font-size: 0.7em;
  padding-top: 0.4em;
  padding-bottom: 0.4em;
  border-bottom: 1px solid #393d4e;
}

#drawer-title > button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

#drawer-close {
  cursor: pointer;
}

.player {
  height: 5em;
  width: 14em;
  border-radius: 1em;
  font-size: 0.7em;
  display: flex;
  z-index: 1001;
}

.player .info {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0.5em 1em;
  width: 9em;
  font-weight: bold;
}

.player .status {
  height: 5em;
  width: 5em;
  border-radius: 50%;
  margin-left: -1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.player .status svg {
  font-size: 2.7em;
}

.player.isAppPlayer .status {
  border: 4px solid #444;
}

.player.is-dead {
  -webkit-filter: brightness(0.6);
  filter: brightness(0.6);
}

.player .name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.player .inner-info {
  grid-gap: 0.1em 0.3em;
}

