.leaflet-container {
  height: 450px;
  max-height: calc(100vh - 100px);
  cursor: pointer;
}

.distance-label {
  border-radius: 10px;
  font-size: smaller;
  color: #ebebeb;
  background-color: #393d4e;
  border: 1px solid #22242e;
}

.distance-label::before {
  all: unset;
}

.mc-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px 15px;
}

.mc-container button.no-pointer {
  cursor: default !important;
  pointer-events: none;
}

@media screen and (min-width: 768px) {
  .poker-table {
    min-height: 500px;
  }
}

.poker-table .map {
  width: 100%;
  min-height: 500px;
}

.leave-game {
  position: absolute;
  top: 8px;
  right: 8px;
}

button:disabled {
